@charset "utf-8";
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Lexend:wght@100..900&family=Pacifico&family=Reddit+Sans:ital,wght@0,200..900;1,200..900&display=swap');

* {
    margin: 0;
    padding: 0;
    vertical-align: baseline;
    outline: none;
    border-style: none;
    box-sizing: border-box;
}

.container {
    width: min(100% - 30px, 1110px);
    margin-inline: auto;
}

body { height: 100svh; }


/*----------------------------------------------------*/
/*----------------------HEADER------------------------*/
/*----------------------------------------------------*/

.flexbox { 
    display: flex; 
    height: 100%;
}

header {
    height: 6em;
    background-color: #D8D8DC;
    overflow: hidden;
}

header h1 {
    font-family: Pacifico;
    display: flex;
    align-items: center;
    justify-content: left;
    width: 25%;
}

header nav {
    width: 45%;
    display: grid;
    place-items: center;

    ul {
        display: flex;
        list-style: none;
        gap: 1.8em;

        li {
            font-family: "Reddit Sans", sans-serif;
            padding: 0.5em 1em;
            border-radius: 1.5em;
            cursor: pointer;
        }

        li:hover {
            background-color: #000;
            color: #D8D8DC;
        }
    }
}

header .social-media {
    width: 30%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2em;

    .fa-brands {
        font-size: 2em;
        cursor: pointer;
    }

    .fa-facebook:hover { color:#1877F2; }
    .fa-instagram:hover { color:#E4405F; }
    .fa-whatsapp:hover { color:#25D366; }
}

/*----------------------------------------------------*/
/*----------------------BANNER------------------------*/
/*----------------------------------------------------*/

.banner {
    height: 26em;
    background: center / cover no-repeat;

    .banner-content {
        background-color: rgba(0, 0, 0, 0.5);
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;

        h2 {
            font-size: 5em;
            font-family: "Bebas Neue";
            color: #fff;
        }

        p {
            font-size: 1.3em;
            font-family: "Reddit Sans", sans-serif;
            color: #fff;
        }
    }

    .banner-btn {
        width: 8.8em;
        height: 3.5em;
        margin-top: 3em;
        border-radius: 2em;
        font-weight: 700;
        font-size: 0.9em;
        padding: 1em;
        cursor: pointer;
    }
}

/*----------------------------------------------------*/
/*----------------------GALLERY-----------------------*/
/*----------------------------------------------------*/

.gallery-section {
    background-color: #eee8e8;
    width: 100%;
    height: 44em;

    .gallery-title {
        width: 100%;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        padding: 3em 0;
        margin-bottom: 1em;

        h3 {
            font-size: 1.9em;
            font-family: Lexend;
            font-weight: 700;
        }

        p {
            font-family: "Reddit Sans", sans-serif;
            margin-top: 1em;
            width: 35%;
            word-wrap: break-word;
            text-align: center;
        }
    }

    .packs-row {
        display: flex;
        flex-direction: row;
        overflow: hidden;
        justify-content: center;

        .travel-pack + .travel-pack {margin-left: 2.8em;}
        .travel-pack {
            background-color: #fff;
            width: 20%;
            height: 27em;
            border-radius: 2em;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            img {
                width: 80%;
                height: 70%;
                object-fit: cover;
                border-radius: 1em;
                margin-top: 1.3em;
            }

            p {
                font-family: "Reddit Sans", sans-serif;
                text-align: center;
                height: 10%;
                margin-top: 20px;
                font-weight: 700;
                text-transform: uppercase;
            }
        }

        .info-btn {
            width: 9em;
            height: 3.5em;
            margin-bottom: 1.5em;
            border-radius: 2em;
            cursor: pointer;
            background-color: #000;
            color: #D8D8DC;
            font-weight: 700;
            padding: 12px;
        }
    }
}

/*----------------------------------------------------*/
/*-----------------------FOOTER-----------------------*/
/*----------------------------------------------------*/

footer {
    height: 17em;
    width: 100%;
    background-color: #000;
    margin-bottom: 1em;

    .footer-options {
        display: flex;
        color: #D8D8DC;

        .options-section {
            width: 73%;
            display: flex;
            justify-content: center;
            gap: 5em;
            padding-top: 2em;

            .options {

                h3 {
                    font-family: Lexend;
                    font-weight: 700;
                    font-size: 25px;
                    padding-right: 1em;
                    border-bottom: 1px solid #fff;
                }

                ul {
                    list-style: none;
                    
                    li {
                        margin-top: 1.5em;
                        padding-left: 5px;
                        cursor: pointer;
                        font-family: "Reddit Sans", sans-serif;
                    }

                    li + li {
                        margin-top: 1em;
                    }
                }
            }
        }

        .subscribe-section {
            width: 27%;
            padding-top: 2em;

            h3 {font-family: Lexend;}

            p {
                font-family: "Reddit Sans", sans-serif;
                width: 13em;
                margin: 1em 0;
            }

            .user-input {
                display: flex;
                margin-top: 2em;

                input {
                    height: 2.3em;
                    width: 15em;
                    margin-right: 1em;
                    padding: 0 1em;
                    font-family: "Reddit Sans", sans-serif;
                }
            }

            .subscribe-btn {
                width: 6em;
                font-weight: 700;
                cursor: pointer;
            }
        }
    }


}